import React from 'react';

export default function Calc(props) {

  const btnClick = (e) => {
    const value = e.target.value;
    props.btnClick(value)
  }

  return (
    <div class="calc-container container-fluid bg-dark text-light text-center">
            <div class="align-items-center h-100 w-50 row mx-auto">
            <div class="col">
                <div class="calculator card">
                <input type="text" class="calculator-screen z-depth-1" value={props.valueInput} disabled />
                <div class="calculator-keys">
                    
                <button onClick={btnClick} type="button" class="operator btn btn-info" value="+">+</button>
                <button onClick={btnClick} type="button" class="operator btn btn-info" value="-">-</button>
                <button onClick={btnClick} type="button" class="operator btn btn-info" value="*">&times;</button>
                <button onClick={btnClick} type="button" class="operator btn btn-info" value="/">&divide;</button>  

                <button onClick={btnClick} type="button" value="7" class="btn btn-dark waves-effect">7</button>
                <button onClick={btnClick} type="button" value="8" class="btn btn-dark waves-effect">8</button>
                <button onClick={btnClick} type="button" value="9" class="btn btn-dark waves-effect">9</button>
                <button onClick={btnClick} type="button" value="4" class="btn btn-dark waves-effect">4</button>
                <button onClick={btnClick} type="button" value="5" class="btn btn-dark waves-effect">5</button>
                <button onClick={btnClick} type="button" value="6" class="btn btn-dark waves-effect">6</button>
                <button onClick={btnClick} type="button" value="1" class="btn btn-dark waves-effect">1</button>
                <button onClick={btnClick} type="button" value="2" class="btn btn-dark waves-effect">2</button>
                <button onClick={btnClick} type="button" value="3" class="btn btn-dark waves-effect">3</button>          
                <button onClick={btnClick} type="button" value="0" class="btn btn-dark waves-effect">0</button>

                <button onClick={btnClick} type="button" class="decimal function btn btn-secondary" value=".">.</button>
                <button onClick={btnClick} type="button" class="all-clear function btn btn-danger btn-sm" value="all-clear">AC</button>
        
                <button onClick={btnClick} type="button" class="equal-sign operator btn btn-outline-success" value="=">=</button>
                </div>
                </div>
            </div>
            </div>
        </div>
  );
}